.protection{
  max-width: 1200px;
  margin: 0 auto;
}
.protection__list {
  max-width: 900px;
  margin: 0 auto;
}
.protection__item{
  margin-bottom: 68px;
}
.protection__info{
  display: flex;
  justify-content: space-between;
}

.protection__left{
  width: 230px;
}

.protection__right{
  width: calc(100% - 230px);
}

.protection__date{
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 5px;
}
.protection__time{
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #ED6F4F;
}
.protection__text{
  font-size: 20px;
  font-weight: bold;
}
.protection__video{
  padding-top: 60px;
  iframe{
    width: 100%;
  }
}

.protection--timing{
  .protection__left{
    width: 318px;
    padding: 25px 0;
    background: rgba(76, 178, 120, 0.1);
    border-radius: 16px;
    text-align: center;
    align-self: flex-start;
  }
  .protection__right{
    width: calc(100% - 395px);
    padding-bottom: 30px;
    border-bottom: 1px solid #C5C5C5;
    padding-top: 30px;
  }
  .protection__text{
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
  }
  .protection__date,
  .protection__time{
    font-size: 26px;
  }
}

@media(max-width: 980px){
  .protection--timing .protection__left{
    padding: 0px;
    width: 115px;
    background-color: transparent;
    text-align: left;
  }
  .protection--timing .protection__right{
    width: calc(100% - 115px);
    padding-top: 0px;
    padding-bottom: 0px;
    border-bottom: 0px solid transparent;
  }
  .protection--timing .protection__date, .protection--timing .protection__time{
    font-size: 14px;
    line-height: 20px;
  }
  .protection--timing .protection__text{
    font-size: 13px;
    line-height: 20px;
  }
  .protection--timing .protection__item{
    padding-bottom: 36px;
    border-bottom: 1px solid #C5C5C5;
    margin-bottom: 36px;
  }
}
@media(max-width: 600px){
  .protection__item {
    margin-bottom: 15px;
  }
  .protection__video{
    padding-top: 37px;
    margin-bottom: 20px;
  }
  .protection__left{
    width: 135px;
  }
  .protection__date,
  .protection__time{
    font-size: 13px;
    line-height: 20px;
  }
  .protection__right{
    width: calc(100% - 135px);
  }
  .protection__text{
    font-size: 13px;
  }
  .protection .h2{
    text-align: center;
  }
  .inner{
    padding-top: 67px;
  }
}