.jury__list {
  display: flex;
  margin-right: -53px;
  flex-wrap: wrap;
}

.jury__item{
  width: calc(33.3% - 53px);
  margin-right: 53px;
  margin-bottom: 60px;
  height: 540px;
  position: relative;
  overflow: hidden;
  border-radius: 14px;
  &:hover{
    .jury__panel{
      top: 120px;
    }
  }
}

.jury__panel {
  position: absolute;
  top: 100%;
  width: 100%;
  margin-top: -120px;
  transition: .4s;
  height: 100%;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #005BDE;
    mix-blend-mode: multiply;
  }
}

.jury__name{
  height: 120px;
  overflow: hidden;
  padding-left: 31px;
  padding-right: 31px;
  display: flex;
  align-items: center;
  color: #fff;
  position: relative;
  z-index: 10;
  font-weight: bold;
  font-size: 28px;
  line-height: 30px;
}


.jury__text{
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
  padding: 0 31px 34px;
  position: relative;
  z-index: 10;
}

.swiper{
  display: none;
}

.swiper-pagination{
  text-align: left;
}

.swiper-pagination-bullet-active{
  background-color: #2CE77D;
}

.jury-lk{
  max-width: 1200px;
  margin: 0 auto;
}

.jury-lk__panel{
  display: flex;
  padding-bottom: 60px;
  margin-bottom: 60px;
  border-bottom: 1px solid #C5C5C5;
}

.jury-lk__right {
  width: 400px;
}

.jury-lk__left{
  width: calc(100% - 438px);
  padding-right: 38px;
}

.jury-lk__link{
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #005BDE;
  margin-bottom: 25px;
  display: block;
  padding-top: 20px;
  transition: .3s;
  &:hover{
    color: #2CE77D;
  }
}

.jury-lk__names {
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
}

.jury-lk__right{
  display: flex;
  justify-content: space-between;
}

.jury-lk__number{
  width: calc(50% - 20px);
}

.jury-lk__number-panel{
  width: 100%;
  height: 180px;
  background: rgba(76, 178, 120, 0.1);
  border-radius: 16px;
  font-weight: 600;
  font-size: 40px;
  line-height: 30px;
  color: #005BDE;
  display: flex;
  justify-content: center;
  align-items: center;
  &.--disabled{
    background: #FDEFD6;
  }
}

.jury-lk__number-text{
  text-align: center;
  font-size: 18px;
}


@media(max-width: 1200px){
  .jury__item{
    width: calc(50% - 53px);
  }
}

@media(max-width: 980px){
  .jury-lk__left{
    width: 100%;
    padding-right: 0px;
  }
  .jury-lk__panel{
    display: block;
  }
  .jury-lk__link{
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 20px;
  }
  .jury-lk__names{
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 25px;
  }
  .jury-lk__number-panel{
    height: 140px;
    order: -1;
  }
  .jury-lk__right{
    max-width: 320px;
    width: 100%;
  }
  .jury-lk__number{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .jury-lk__number-text{
    font-weight: 500;
    font-size: 13px;
    line-height: 30px;
    color: #A2A6AB;
  }
}

@media(max-width: 768px){
  .jury__item{
    width: 256px;
    height: 340px;
  }
  .jury__item:hover .jury__panel{
    top: auto;
  }
  .jury__panel {
    bottom: 0;
    top: auto;
    margin-top: 0px;
    height: auto;
  }
  .jury__name{
    height: auto;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 20px;
  }
  .jury__text{
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 16px;
    font-size: 13px;
    line-height: 20px;
  }
  .jury .h2{
    text-align: center;
  }
  .jury__list{
    display: none;
  }
  .jury .swiper{
    display: block;
  }
}