
.news__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -85px;
}

.news__item.--big{
  width: calc(100% - 85px);
  margin-right: 85px;
  padding: 30px 33px 40px;
  background: #EEF8F2;
  border-radius: 14px;
  margin-bottom: 90px;
  transition: .4s;
  &:hover{
    background-color: #2CE77D;
    .news__date{
      background-color: #2CE77D;
      color: #fff;
    }
    .news__title,
    .news__text {
      color: #fff;
    }
  }
  .news__pic{
    margin-bottom: 36px;
  }
  .news__pic img{
    border-radius: 8px;
  }
  .news__description{
    display: flex;
    justify-content: space-between;
  }
  .news__date{
    height: 120px;
    width: 305px;
    background-color: #fff;
    font-weight: bold;
    font-size: 26px;
    line-height: 35px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .4s;
  }
  .news__info{
    width: calc(100% - 432px);
    .news__date{
      display: none;
    }
  }
  .news__title{
    font-weight: bold;
    font-size: 26px;
    line-height: 30px;
    color: #005BDE;
    margin-bottom: 41px;
    transition: .4s;
  }
  .news__text {
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    transition: .4s;
  }
}

.news__item.--small{
  width: calc(50% - 85px);
  margin-right: 85px;
  border-radius: 14px;
  transition: .4s;
  margin-bottom: 60px;
  &:hover{
    background-color: #2CE77D;
    .news__pic{
      background-color: #2CE77D;
    }
    .news__title,
    .news__date,
    .news__text{
      color: #fff;
    }
  }
  .news__pic{
    padding: 30px;
    background: #EEF8F2;
    border-radius: 14px;
    img{
      border-radius: 8px;
    }
    overflow: hidden;
    transition: .4s;
  }
  .news__description{
    padding-top: 21px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 22px;
  }
  .news__title{
    font-weight: 600;
    font-size: 26px;
    line-height: 30px;
    color: #005BDE;
    margin-bottom: 13px;
    transition: .4s;
    text-transform: uppercase;
  }

  .news__date{
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    color: #A2A6AB;
    transition: .4s;
  }

  .news__text{
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    transition: .4s;
  }
}

.news__slider {
  padding-bottom: 55px;
  overflow: hidden;
  .swiper-pagination{
    visibility: hidden;
  }
}
.news-inner__info{
  display: flex;
  justify-content: space-between;
  .btn{
    height: 80px;
  }
}
.news-inner__right{
  width: calc(100% - 400px);
}
.news-inner__info .news__date{
  height: 120px;
  width: 305px;
  background-color: #EEF8F2;
  font-weight: bold;
  font-size: 26px;
  line-height: 35px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .4s;
}

.news-inner__text{
  margin-bottom: 60px;
  p{
    font-size: 20px;
    margin-bottom: 30px;
    line-height: 30px;
  }
}



@media(max-width: 980px){
  .news__slider{
    .swiper-pagination{
      visibility: visible;
      text-align: center;
    }
  }
  .news-inner__info{
    display: block;
    .btn{
      height: 60px;
    }
    .news__date{
      display: block;
      height: auto;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: #A2A6AB;
      background-color: transparent;
      padding-bottom: 5px;
    }
  }
  .news-inner__right{
    width: 100%;
  }
  .news-inner__text{
    p{
      font-size: 14px;
      line-height: 20px;
    }
  }
  .news h2{
    text-align: center;
    margin-bottom: 50px;
  }
  .news__item.--big,
  .news__item.--small{
    width: calc(100% - 85px);
    margin-bottom: 52px;
    padding: 13px 13px 40px;
    &:hover{
      .news__info .news__date{
        color: #fff;
      }
    }
    .news__pic{
      border-radius: 8px;
      img{
        min-height: 168px;
      }
    }
    .news__pic{
      padding: 0;
      margin-bottom: 23px;
      img{
        width: 100%;
      }
    }
    .news__description{
      display: block;
    }
    .news__date.--mobile{
      display: none;
    }
    .news__info{
      width: 100%;
    }
    .news__info .news__date{
      display: block;
      height: auto;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      color: #A2A6AB;
      background-color: transparent;
    }
    .news__date{
      display: block;
      height: auto;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      color: #A2A6AB;
      background-color: transparent;
    }
    .news__title{
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 20px;
    }
    .news__text{
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
    }
    .news__description{
      padding: 0;
    }
  }
 
}