.winners__form{
  padding-top: 100px;
  padding-bottom: 89px;
  margin-bottom: 90px;
  background-color: #E6EFFC;
}
.search{
  margin-bottom: 60px;
}

.search__left{
  width: calc(66.6% - 8px);
  position: relative;
  .input{
    margin-bottom: 0px;
  }
}
.search__btn{
  position: absolute;
  height: 100%;
  right: 18px;
  display: flex;
  align-items: center;
  top: 0;
}
.switch__content{
  max-width: 289px;
}

.selects {
  display: flex;
  margin-right: -30px;
  .select{
    width: calc(33.33% - 30px);
    margin-right: 30px;
  }
}

.switch{
  background: #FFFFFF;
  border: 1px solid #005BDE;
  border-radius: 33px;
  width: 56px;
  height: 28px;
  position: relative;
  cursor: pointer;
  margin-right: 30px;
  &.active:after{
    left: 32px;
  }
  &:after{
    transition: .3s;
    content: "";
    position: absolute;
    left: 4px;
    top: 4px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #005BDE;
  }
}

.switch__text{
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #005BDE;
}

.winners__ico svg{
  fill: #2CE77D;
}


.winners__list{
  display: flex;
  margin-right: -87px;
  flex-wrap: wrap;
}

.winners__item{
  display: block;
  width: calc(50% - 87px);
  margin-bottom: 90px;
  margin-right: 87px;
  padding: 36px 34px;
  background: #EEF8F2;
  border-radius: 14px;
  position: relative;
  transition: .4s;
  &:hover{
    background-color: #06C358;
    .winners__title,
    .winners__category{
      color: #fff;
    }
    .winners__ico svg{
      fill: #fff;
    }
  }
}

.winners__title{
  font-weight: 600;
  font-size: 26px;
  line-height: 30px;
  color: #005BDE;
  min-height: 220px;
  transition: .4s;
}

.winners__region,
.winners__category-name,
.winners__category{
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
}

.winners__category{
  color: #ED6F4F;
  transition: .4s;
}

.winners__ico{
  position: absolute;
  right: 58px;
  bottom: 40px;
  svg{
    transition: .4s;
  }
}


@media(max-width: 980px){
  .winners__item{
    width: calc(100% - 87px);
    margin-bottom: 40px;
    padding: 23px;
  }
  .winners__title{
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    min-height: auto;
    margin-bottom: 20px;
  }
  .winners__ico{
    position: static;
    width: 42px;
    margin-bottom: 17px;
    .svgsprite._win-ico-1{
      width: 42px;
      height: 42px;
    }
    .svgsprite._win-ico-2{
      width: 42px;
      height: 42px;
    }
  }
  .winners__region,
  .winners__category-name,
  .winners__category{
    font-size: 14px;
    line-height: 20px;
  }
  .switch__content {
    display: none;
  }
  .search__left{
    width: 100%;
  }
  .search{
    margin-bottom: 20px;
  }
  .selects{
    display: block;
    margin-right: 0px;
    .select{
      width: 100%;
    }
  }
  .winners__form{
    padding: 0;
    background-color: transparent;
  }
  .winners{
    h2{
      text-align: center;
    }
  }
}